<template>
    <div class="EchartPractice">
        <div id="main"></div>
    </div>
</template>

<script>
export default {
    name: "EchartPractice",
    data() {
        return {};
    },
    methods: {
        drawChart() {
            var myChart = this.$echarts.init(document.getElementById("main"));
            this.$echarts.registerMap("zhejiang", this.commonJson);
            var option = {
                series: [
                    {
                        map: "zhejiang",
                        type: "map",
                        aspectScale: 1.0,
                        selectedMode: "single", //选择类型,
                        hoverable: false, //鼠标经过高亮
                        roam: true, //鼠标滚轮缩放
                        itemStyle: {
                            normal: {
                                borderWidth: 1,
                                borderColor: "#ffffff", //区域边框色
                                areaColor: "#FFDAB9", //区域背景色
                                label: {
                                    show: true,
                                    textStyle: {
                                        color: "#6495ED", //文字颜色
                                        fontSize: 18, //文字大小
                                    },
                                },
                            },
                            emphasis: {
                                // 选中样式
                                borderWidth: 1,
                                borderColor: "#00ffff",
                                color: "#ffffff",
                                label: {
                                    show: true,
                                    textStyle: {
                                        color: "#ff0000",
                                    },
                                },
                            },
                        },
                    },
                ],
            };
            myChart.setOption(option);
        },
    },
    mounted() {
        this.$axios
            .get(
                "https://geo.datav.aliyun.com/areas/bound/" +
                    this.$route.query.id +
                    "_full.json"
            )
            .then((res) => {
                this.commonJson = res.data;

                this.$nextTick(() => {
                    this.drawChart();
                });
            });
    },
};
</script>

<style scoped>
#main {
    width: 1000px;
    height: 600px;
    margin: auto;
    margin-top: 50px;
}
</style>